@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');

.item {
  width: 90%;
  height: 60px;
  margin: 0 auto;
  display: flex;
  
}
.itemValue {
  width: 80%;
  display: flex;
  align-items: center;
  padding-left: 15px;
  font-size: 1.3rem;
  font-family: 'Noto Sans KR', sans-serif;
}
.removeBtn {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  color: red;
  cursor: pointer;
}