@import url('https://fonts.googleapis.com/css2?family=Manrope&display=swap');

.btn {
  width: 200px;
  height: 60px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 40px;
  border-radius: 15px;
  color: rgb(255, 85, 85);
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 900;
  font-family: 'Manrope', sans-serif;
}