@import url('https://fonts.googleapis.com/css2?family=Secular+One&display=swap');

.btn {
  width: 400px;
  height: 80px;
  background: #FFFFFF;
  border-radius: 10px;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 1s;
}
.btnhover {
  width: 400px;
  height: 80px;
  background: #FFFFFF;
  border-radius: 10px;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .5s;
}
.btnhover:hover {
  transform: translate(50%);
}
a {
  color: black;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 20px 60px;
  font-family: 'Secular One', sans-serif;
}