.inputBox {
  width: 600px;
  height: 70px;
  display: flex;
  margin: 0 auto 30px;
}
.input {
  width: 80%;
  border: none;
  padding-left: 20px;
  font-size: 1.5rem;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
.input:focus {
  outline: none;
}
.btn {
  width: 20%;
  height: 100%;
  background-color: green;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  cursor: pointer;
}